.footer {
 margin-top: 60px;
 background-color: #131313;
 padding: 59px 0;
 color: #666666; }
.footer-policy-link {
 font-size: 12px; }
.footer p {
 margin-bottom: 12px; }
.footer a {
 color: #666666;
 text-decoration: none; }
.footer a:hover {
 color: #fff; }
.footer small {
 display: block;
 margin-top: 56px;
 line-height: 16px;
 font-size: 10px;
 max-width: 80%; }
.footer ul {
 padding: 0;
 list-style: none; }
.footer ul {
  li {
   line-height: 32px; } }
.footer-contacts, .footer-social-links {
 text-align: right; }
.footer-contacts_phone {
 font-weight: 400;
 font-size: 20px;
 color: #009adf; }
.footer-social-links li {
 display: inline-block; }
.footer-social-links li {
  a {
   display: inline-block;
   width: 42px;
   height: 42px;
   background-color: rgba(255, 255, 255, 0.1);
   text-align: center;
   border-radius: 50%;
   line-height: 42px;
   margin-left: 10px;
   -weebkit-transition: all 0.2s;
   transition: all 0.2s; } }
.footer-social-links li {
  a:hover {
   background-color: rgba(255, 255, 255, 0.9); } }
